<!--  -->
<template>
  <div class="mypage">
 <div style="float: right;
    margin-right: 5%;
    height: 50px;
    margin-top:2%">

    <el-dropdown>
      <span class="el-dropdown-link">
       <img src="../assets/default.png" width="40px" style="border-radius:50px;margin:-10px"/>
       <span style="margin-left:20px;color:white">{{nickname}}</span>
      </span>
      <el-dropdown-menu slot="dropdown">
        <el-dropdown-item icon="el-icon-user-solid">
          <span>{{nickname}}</span>
          </el-dropdown-item>
          <el-dropdown-item icon="
el-icon-s-home">
          <router-link to="/Calendar"></router-link></el-dropdown-item>
        <el-dropdown-item icon="el-icon-remove">
          <router-link to="/"></router-link></el-dropdown-item>
      </el-dropdown-menu>
    </el-dropdown>

          </div>
    
    <el-button circle 
    style="float: right;
    margin-right:65%;
    margin-top:1.2%;
    background:#4682B4;
    border:#4682B4"> 
    </el-button>
          <span style="
              position: absolute;
    top: 10%;
    left: 40%;
    color: white;
    font-weight: bold;
    font-size: 2em;"></span>
    <div class="leftbox">
         <el-button circle icon="el-icon-menu" @click="showMenu" style="margin-left: 10px;margin-top:1%"></el-button>
        <el-menu default-active="2" 
        class="el-menu-vertical-demo"
        background-color="#005792" 
        style="
    color: white;
    font-size: 30px;
    height:1000px;
    overflow: auto;
    float:left"
     @open="handleOpen" @close="handleClose" :collapse="isCollapse"
     collapse-transition="false"
       >
       <el-menu-item 
       style="height:80px">
            <template>
           <img src="../assets/logo.png" style="width:45px;
    margin-top:30px;
    margin-left:-10px;"/>
        <img src="../assets/logo3.png" style="width:80px;
    margin-top:30px;
    margin-left:10px;" slot="title"/>
            </template>
       </el-menu-item>
            <el-menu-item index="1"  @click="menuChange('RoomView')">
            <i class="el-icon-date"></i>
            <span slot="title"></span>
          </el-menu-item>
            <el-menu-item index="2"  @click="menuChange('roomBook')">
            <i class="el-icon-phone-outline"></i>
            <span slot="title"></span>
          </el-menu-item>
        <el-menu-item index="3"  @click="menuChange('HelloWorld')">
            <i class="el-icon-news"></i>
            <span slot="title"></span>
          </el-menu-item>
          <!-- </el-menu-item> -->
            <el-menu-item index="4"  @click="menuChange('roomBook')">
            <i class="el-icon-phone-outline"></i>
            <span slot="title"></span>
          </el-menu-item>
            <el-menu-item index="5"  @click="menuChange('roomBook')">
            <i class="el-icon-phone-outline"></i>
            <span slot="title"></span>
          </el-menu-item>
          <el-menu-item index="6" @click="menuChange('message')">
            <i class="el-icon-bell"></i>
            <span slot="title" style="margin-left:-8px">
              
            </span>
          </el-menu-item>
            <el-menu-item index="7"  @click="menuChange('perfectInformation')">
            <i class="el-icon-phone-outline"></i>
            <span slot="title"></span>
          </el-menu-item>
        </el-menu>
        <div class="left">
        <router-view class="left"></router-view>
        </div>
    </div>
  </div>
</template>
<script>
import axios from 'axios'
import $ from "jquery";
export default {
  data() {
    return {
        isCollapse:true,
      nickname:"",
      activeIndex: "1",
      activeIndex2: "1"
    };
  },
  methods: {
showMenu(){
  var box=$('div .left');
  console.log(box);
   if (this.isCollapse==true)
   {
    box[0].setAttribute("style"," background:transparent;width:75%;height: 90%;position: absolute;top: 20%;left: 20%;border-radius: 6px;")
    this.isCollapse=false;}
  else
   {
     box[0].setAttribute("style"," background:transparent;width:80%;height: 90%;position: absolute;top: 20%;left:12%;border-radius: 6px;")
     this.isCollapse=true;}
     },
    menuChange:function(item){
       this.$router.push({ path: '/'+item,name:item})
    }
  },
  created(){
    //http://111.231.200.75/api/myPage
     axios.get(`http://111.231.200.75/api/myPage`, 
            {params:{email:this.$cookie.get('email')}}).then(response => {
              this.nickname=response.data.nickname;
             
  });
  this.$router.push({ path:'Calendar',name:'Calendar'})
  }
};
</script>
<style>
 .mypage{
  height:300px;
  background:steelblue;
}
 .el-menu-vertical-demo:not(.el-menu--collapse) {
    width: 200px;
    min-height: 400px;
  }
.toleft{
  margin-left: 5px;
  color:white;
}
.item {
  margin-top: 10px;
  margin-right: 40px;
}

.mypage /deep/ .el-col-12 {
  width: 20%;
  position:fixed;
  height:104%;
}
.mypage /deep/ .el-menu {
  border-right: 0px;
}
.mypage /deep/ .el-menu--horizontal>.el-menu-item:not(.is-disabled):focus, .el-menu--horizontal>.el-menu-item:not(.is-disabled):hover, .el-menu--horizontal>.el-submenu .el-submenu__title:hover
{
  background-color: rgba(250,250,250,0.3)
}
.left {
   background:transparent;
    width:85%;
    height: 90%;
    position: absolute;
    top: 16%;
    left: 5%;
    border-radius: 6px;
}
.leftbox /deep/ .el-menu-item-group{
      background:rgb(0, 87, 146)
;
}
.leftbox /deep/ .el-menu-item {
    font-size: 16px;
    color: white;
    padding: 0 20px;
    cursor: pointer;
    -webkit-transition: border-color .3s,background-color .3s,color .3s;
    transition: border-color .3s,background-color .3s,color .3s;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;}
.leftbox /deep/ .el-submenu__title{
    font-size: 16px;
    color:white;
    padding: 0 20px;
    cursor: pointer;
    -webkit-transition: border-color .3s,background-color .3s,color .3s;
    transition: border-color .3s,background-color .3s,color .3s;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
  }
  .mypage /deep/ .el-badge__content{background-color: #F56C6C;
    border-radius: 10px;
    display: inline-block;
    font-size: 12px;
    height: 18px;
    line-height: 18px;
    padding: 0 6px;
    text-align: center;
    white-space: nowrap;
    position: absolute;
    top: 0px;}
  .leftbox /deep/ .el-submenu__title i {
    color: white;
}
.leftbox /deep/ .el-menu-item i {
    color:white;
}
.leftbox /deep/ .el-submenu .el-menu-item {
    height: 50px;
    line-height: 50px;
    padding: 0 45px;
    min-width: 80%;
    background:rgb(0, 87, 146);
    font-size: 14px
}
.leftbox /deep/ .el-menu-item-group__title {
    padding: 7px 0 7px 20px;
    line-height: normal;
    font-size: 12px;
    color: #909399;
    background:rgb(0, 87, 146)

}
.leftbox /deep/ .el-menu-item:focus,.el-menu-item:hover {
    outline: 0;
    background-color: rgba(255,255,255,0.3);
}
.leftbox /deep/ .el-submenu__title:hover {
    background-color: rgba(255,255,255,0.3);
}
.item:focus,.item:hover{
  background-color: rgba(255,255,255,0.3);
}
.mypage /deep/ .el-menu.el-menu--horizontal {
  border:none;
}
.mypage /deep/ .el-menu--horizontal>.el-menu-item.is-active {
    border-bottom: 2px solid rgb(0, 87, 146);
    color: #303133;
}
.item {
  margin-top: 10px;
  margin-right: 40px;
  color: white;
}
.icon {
width: 20px;
    height: 20px;
    vertical-align: -0.3em;
    fill:blue;
    overflow: hidden;
    margin-left: 0px
}
</style>


