<template>
<!-- <div class="Message">
  <el-timeline 
  style="margin-top: 20px;
    overflow: auto;
    width: 850px;">
    <el-timeline-item 
    placement="top"
     v-for="(message,index) in messages"
   :key="index"
  :timestamp="message.time"
   :color="message.color"
    size="large"
    :icon="message.icon">
      <el-card>
        <h4>{{message.theme}}</h4>
        <p>{{message.content}}<router-link :to="{path:'/pollDetails.vue',query:{activityId:message.activityId}}" style="float:right"></router-link></p>
      </el-card>
