<div class="Message">
  <el-timeline>
    <el-timeline-item timestamp="2018/4/12" placement="top">
      <el-card>
        <h4> Github </h4>
        <p>  2018/4/12 20:46</p>
      </el-card>
    </el-timeline-item>
    <el-timeline-item timestamp="2018/4/3" placement="top">
      <el-card>
        <h4> Github </h4>
        <p>  2018/4/3 20:46</p>
      </el-card>
    </el-timeline-item>
    <el-timeline-item timestamp="2018/4/2" placement="top">
      <el-card>
        <h4> Github </h4>
        <p>  2018/4/2 20:46</p>
      </el-card>
    </el-timeline-item>
  </el-timeline>
</div>
</template>

<script>
import axios from 'axios'
export default {
  data () {
    return {
        messages:[]
    };
  },

  components: {},

  computed: {},

 mounted(){
   this.$notify.info({
          title: 'Help',
          message: ''
        });
           axios.get(`http://111.231.200.75/api/getMessage`, 
            {params:{email:this.$cookie.get('email')}}).then(response => {
              //var getevents=[];
              //console.log(response.data)
          for (var i = 0; i < response.data.length; i++) {
          switch(response.data[i].type)
          {
          case "invite":response.data[i].color="#E9967A";
                      response.data[i].icon="el-icon-location-outline";break;
          case "dopoll":response.data[i].color="#9ACD32";
                      response.data[i].icon="el-icon-date";break;
          case "request":response.data[i].color="#FF9800";
                      response.data[i].icon="el-icon-phone-outline";break;
          case "message":response.data[i].color="#FF9800";
                      response.data[i].icon="el-icon-tickets";break;
          }
        }
        this.messages=response.data;
        /*this.$notify.info({
          title: 'Help',
          message: ''
        })*/
      
      }
            )
  },

  methods: {}
}

</script>
<style scoped>
.Message /deep/ .el-timeline-item__node--large{
    left: -17px;
    width: 45px;
    height: 45px;
}
.Message /deep/ .el-timeline-item__timestamp{
  color: white;
    line-height: 3;
    font-size: 17px;
    margin-left: 20px;
}
.Message{
  overflow:auto;
    width:100%;
    height: 100%;
    position: absolute;
    top: 0%;
    left: 0%;
    border-radius: 6px;
}
::-webkit-scrollbar { width: 5px;}
::-webkit-scrollbar-track-piece { background-color: #ffffff;}
::-webkit-scrollbar-thumb { height: 30px; background-color: rgb(192, 192, 192); border-radius: 3px;}
</style>


