<!--  -->
<template>
<div>
  <el-button @click="second_Step" ></el-button>
  <el-table
    ref="filterTable"
    :data="tableData"
    style="width: 100%">
    <el-table-column
      prop="date"
      label=""
      sortable
      width="180"
      column-key="date"
      :filters="[{text: '2016-05-01', value: '2016-05-01'}, {text: '2016-05-02', value: '2016-05-02'}, {text: '2016-05-03', value: '2016-05-03'}, {text: '2016-05-04', value: '2016-05-04'}]"
      :filter-method="filterHandler"
    >
    </el-table-column>
    <el-table-column
      prop="name"
      label=""
      width="180">
    </el-table-column>
    <el-table-column
      prop="address"
      label=""
      :formatter="formatter">
    </el-table-column>
    <el-table-column
      prop="tag"
      label=""
      width="100"
      :filters="[{ text: '', value: '' }, { text: '', value: '' }]"
      :filter-method="filterTag"
      filter-placement="bottom-end">
      <template slot-scope="scope">
        <el-tag
          :type="scope.row.tag === '' ? 'primary' : 'success'"
          disable-transitions>{{scope.row.tag}}</el-tag>
      </template>
    </el-table-column>
  </el-table>
</div>
</template>

<script>
  export default {
    data() {
      return {
        tableData: [{
          date: '2016-05-02',
          name: '',
          address: ' 1518 ',
          tag: ''
        }, {
          date: '2016-05-04',
          name: '',
          address: ' 1517 ',
          tag: ''
        }, {
          date: '2016-05-01',
          name: '',
          address: ' 1519 ',
          tag: ''
        }, {
          date: '2016-05-03',
          name: '',
          address: ' 1516 ',
          tag: ''
        }]
      }
    },
    methods: {
      resetDateFilter() {
        this.$refs.filterTable.clearFilter('date');
      },
      clearFilter() {
        this.$refs.filterTable.clearFilter();
      },
      formatter(row, column) {
        return row.address;
      },
      filterTag(value, row) {
        return row.tag === value;
      },
      filterHandler(value, row, column) {
        const property = column['property'];
        return row[property] === value;
      },
      second_Step() {
        this.$router.push({ path: '/RoomView'})
    },
    }
  }
</script>


