import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import login from '@/components/login'
import register from '@/components/register'
import navmenu from '@/components/navmenu'
import RoomView from '@/components/RoomView'
import message from '@/components/message'
import perfectInformation from '@/components/perfectInformation'
import graphBook from '@/components/graphBook'
// import indexbar from '@/components/indexbar'
const indexbar = () => import('@/components/indexbar.vue')
const roomBook = () => import('@/components/roomBook.vue')


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'login',
      component: login
    },
    {
      path: '/login',
      name: 'login',
      component: login
    },
    {
      path: '/register',
      name: 'register',
      component: register
    },
    {
      path: '/navmenu',
      name: 'navmenu',
      component: navmenu
    },
    {
      path: '/RoomView',
      name: 'RoomView',
      component: RoomView
    },
    {
      path: '/message',
      name: 'message',
      component: message
    },
    {
      path: '/perfectInformation',
      name: 'perfectInformation',
      component: perfectInformation
    },
    {
      path: '/indexbar',
      name: 'indexbar',
      component: indexbar,
      // alias:'/indexbar'
    },
    {
      path: '/roomBook',
      name: 'roomBook',
      component: roomBook
    },
    {
      path: '/graphBook',
      name: 'graphBook',
      component: graphBook
    }
  ]
})



