<!--  -->
<template>
  <div class="hello">
  <el-button size="medium" @click="loginFormVisible=true"></el-button>
  <el-dialog class="loginform"  title="Meet" :visible.sync="loginFormVisible">
    <el-button class="linkbutton" type=text @click="shiftForm('register')"></el-button>
    <el-form :model="loginForm" status-icon :rules="loginRules" ref="loginForm">
      <el-form-item prop="username"  style="width: 290px;">   
        <el-input v-model="loginForm.username" placeholder=""></el-input>
        <el-button type="primary" icon="el-icon-message"></el-button>
      </el-form-item> 
      <el-form-item prop="pass"  style="width: 290px;position: relative;top: -36px;">   
        <el-input v-model="loginForm.pass" placeholder=""></el-input>
        <el-button type="primary" icon="el-icon-edit"></el-button>
      </el-form-item>  
      <el-form-item>
          <el-checkbox label="" name="type"></el-checkbox>
        </el-form-item>
        <el-form-item>
          <router-link to></router-link>
        </el-form-item> 
        <el-button type="primary" @click="submitLoginForm('loginForm')"> </el-button>
    </el-form>
  </el-dialog>
  </div>
</template>

<script>
import https from '../https.js'
import $ from "jquery";
export default {
    data() {
      var checkemail = (rule, value, callback) => {
        if (!value) {
          return callback(new Error(""));
        }
        setTimeout(() => {
          if (value !== "") {
            var reg = /^[A-Za-z0-9\u4e00-\u9fa5]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/;
            if (!reg.test(value)) {
              callback(new Error(""));
            } else {
              callback();
            }
          }
        }, 1000);
      };
      //
      var validatePass = (rule, value, callback) => {
        if (value === "") {
          callback(new Error(""));
        } else {
          callback();
        }
      };
      var rcheckemail = (rule, value, callback) => {
        if (!value) {
          return callback(new Error(""));
        }
        setTimeout(() => {
          if (value !== "") {
            var reg = /^[A-Za-z0-9\u4e00-\u9fa5]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/;
            if (!reg.test(value)) {
              callback(new Error(""));
            } else {
              callback();
            }
          }
        }, 1000);
      };
      var rvalidatePass = (rule, value, callback) => {
        if (value === "") {
          callback(new Error(""));
        } else {
          callback();
        }
      };
      var rvalidatename = (rule, value, callback) => {
        if (value === "") {
          callback(new Error(""));
        } else {
          callback();
        }
      };
      var rcheckpass = (rule, value, callback) => {
        if (value === "") {
          callback(new Error(""));
        } else if (value !== this.registForm.rpass) {
          callback(new Error("!"));
        } else {
          callback();
        }
      };
      return {
        loginForm:{
          username:"",
          pass:""
        },
       //
        loginRules: {
          pass: [{ validator: validatePass, trigger: "blur" }],
          username: [{ validator: checkemail, trigger: "blur" }]
          //trigger: blur 
        },
        loginFormVisible: false,
      }
    },
    methods:{
      shiftForm(formType){
        if(formType=='register'){
          console.log(3);
          this.loginFormVisible=false;
          this.registerFormVisible = true;
        }
        if(formType=='login'){
           console.log(2);
          this.loginFormVisible=true;
          this.registerFormVisible = false;
        }
      },
      submitLoginForm(loginForm){
        this.loginFormVisible=false;
        var result="";
        // alert("");
        this.$refs[loginForm].validate((valid)=>{
          if(valid){
            let postData = this.$qs.stringify({
              username:this.loginForm.username,
              password:this.loginForm.pass
            });
            $.ajax({
              url: "/api/login",
              type: "post",
              data: postData,
              params:{"contentType": "application/json;charset=utf-8"},
              dataType: "json",
              success: function(data) {
                if (data.Result == "true" || data.Result == true) {
                //   result = "true";
                  // var json= JSON.stringify(data);
                  // alert(json);
                  //  alert(JSON.stringify(username));
                  alert("1");
                  console.log(data);
                  this.$router.push({ path: '/indexbar'})
                }
                // if (data.Result == "no username") {
                //   alert("");
                //   console.log(2);
                // }
                // if (data.Result == "false" || data.Result == false) {
                //   alert("");
                //   console.log(3);
                // }
                // if (data.Result == "unactive") {
                //   alert("");
                //   console.log(4);
                // }
            },
            error: function(data) {
              alert("");
            }
            });
          }
        })
        
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.loginform{
  margin-top: 50px;
  color:white;
}
.linkbutton{
  position:absolute;
  top:15px;
  left: 110px;
}
.el-button--primary {
    color: #FFF;
    background-color: lightgray;
    border-color: lightgray;
    left: 137px;
    position: relative;
    top: -40px;
}
</style>



